body, html{
  width: 100vw;
  margin: 0;
  overflow-x: hidden;
  background: #000;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:focus, input{
  outline: none;
}

@font-face{
  font-family: druk;
  src: url(../assets/DrukWide-Medium.otf);
}

a{
  text-decoration: none;
  display: block;
  font-size: 12px;
  font-weight: 100;
  text-transform: uppercase;
}

button{
  border: none;
  cursor: pointer;
}

h1, h2, h3, h4, h5, p, a, button, span{
  font-family: druk;
  color: #fff;
  text-transform: uppercase;
}

h1{
  font-size: 6vw;
  text-transform: uppercase;
  color: #fff;
}

.cta{
  padding: 20px 40px;
  background: #000;
  color: #fff;
  display: block;
  width: auto;
  margin: auto;
}

.para{
  font-size: 25px;
  opacity: .5;
}


.o-row{
  display: block;
  overflow: hidden;

  & > span{
    display: block;
    white-space: nowrap;
    transition: .4s cubic-bezier(0.215, 0.61, 0.355, 1)
  }
}

.o-video-wrapper{
  overflow: hidden;
  width: inherit;
  height: inherit;
  width: 100%;

  video{
    width: 100%;
  }
}

.o-box{
  position: relative;

  .o-video-wrapper{
    transition: .6s;
    position: relative;

    .video-cover{
      position: absolute;
      width: 100%;
      height: 100%;
      background: #000;
      top: 0;
      left: 0;
      opacity: 1;
      z-index: 2;
      transition: .4s;
    }
  }

  .o-box__left, .o-box__right{
    display: block;
    position: absolute;
    top: 0;
    height: 100%;
    transform: scaleY(0);
    transform-origin: top;
    width: 1px;
    background: #fff;
    z-index: 3;
  }

  .o-box__right{
    right: 0;
  }

  .o-box__left{
    left: 0;
  }

  .o-box__top, .o-box__bottom{
    position: absolute;
    display: block;
    height: 1px;
    width: 100%;
    transform: scaleX(0);
    transform-origin: center;
    left: 0;
    background: #fff;
  }

  .o-box__top{
    top: 0;
  }

  .o-box__bottom{
    bottom: 0;
  }
}



//MENU CSS

nav{
  width: 100%;
  display: flex;
  padding: 6vh 8vw;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 100;
  border-bottom: 0;  
}

.logo{
  font-family: logo;
  font-size: 14px;
  color: #fff;
  margin-top: -6px;
  font-family: druk;
}

.nav_desktop{
  display: none;
  align-items: center;
  position: relative;
  width: 100%;
  justify-content: flex-end;

  div{
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-72%, -50%);

    a{
      padding: 0 20px;
      color: #fff;
    }
  }

  .nav_cta{
    padding: 4vh 5vw;
    background: #fff;
    color: #000;
    // display: block;
    // width: auto;
    // margin: auto;
  }
}

#nav_mobile{
  width: 100vw;
  height: 100vh;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;

  div{
    padding: 20vh 8vw;
    z-index: 4;
  }

  video{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: .4s;
  }

  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    transition: .6s;
  }

  & > div > a > span{
    display: block;
    overflow: hidden;
    transition: .4s;
  }

  & > div > a > span > span{
    display: block;
    transition: .6s;
  }

  a{
    color: #fff;
    margin: 22px 0;
    font-size: 24px;

    &:last-child{
      margin-top: 55px;
      font-size: 12px;
      position: absolute;
      bottom: 6vh;
      left: 8vw;
      background: #fff;
      transition: .4s;

      span{
        color: #000;
      }
    }
  }
}

.nav_mobile-hidden{
  pointer-events: none;

  video{
    opacity: 0;
  }

  .cta{
    opacity: 0;
  }

  &::before{
    transform: translate(0, -100%);
    transition-delay: .8s;
  }

  & > div > a > span > span{
    transform: translate(0, 100%);
  }
}

.nav_mobile-visible{
  pointer-events: all;

  video{
    opacity: 1;
  }

  &::before{
    transform: translate(0, 0);
    transition-delay: 0;
  }

  & > div > a > span > span{
    transform: translate(0, 0);
    transition-delay: .6s;
  }
}


#js-menu {
  width: 20px;
  height: 30px;
  position: relative;
  cursor: pointer;

  span{
    display: block;
    background-color: #fff;
    transition: .4s;
  }
  #hamburger {
    position:absolute;
    height:100%;
    width:140%;
    transform: translate(-40%, 0);
    span {
      //widows: 100%;
      height:2px;
      margin:5px 0;
      &:nth-child(1){
        transition-delay:.6s;
        width: 60%;
      }
      &:nth-child(2){
        transition-delay:.625s;
      }
    }
  }
  #cross {
    position:absolute;
    height:100%;
    width:100%;
    transform:rotate(45deg);
    span{
      &:nth-child(1){
        height:0%;
        width:2px;
        position:absolute;
        top:-7%;
        left:30%;
        transition-delay:0s;
      }
      &:nth-child(2){
        width:0%;
        height:2px;
        position:absolute;
        left:-14%;
        top:24%;
        transition-delay:.25s;
      }
    }
  }
}

#js-menu.open {
  #hamburger {
    span {
      width:0%;
      &:nth-child(1){
        transition-delay:0s;
      }
      &:nth-child(2){
        transition-delay:.125s;
      }
    }
  }
  #cross {
    span {
      &:nth-child(1){
        height:70%;
        transition-delay:.625s;
      }
    }
    span {
      &:nth-child(2){
        width:100%;
        transition-delay:.375s;
      }
    }    
  }
}

