.c-marquee__text{
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  top: 5vh;
  font-size: 9vw;

  & > span{
    display: inline-block;
    padding-left: 100%;
    animation: marquee 20s linear infinite;
  }

  &:nth-child(2) span{
    animation-delay: 10s;
  }
}



@keyframes marquee{
  from{
    transform: translate(0, 0);
  }
  to{
    transform: translate(-100%, 0);
  }
}

//ABOUT SECTION

.about{
  width: 100vw;
  padding: 20vh 5vw;

  h3{
    font-size: 3vw;
  }

  .about-video{
    margin: 8vh 0;
  }

  .about-text{
