}

.about-text:nth-child(3){
  padding-bottom: 4vh;
}


.mobile{
  display: block;
}

.desktop{
  display: none;
}


//FEATURES

.features{
  padding: 10vh 5vw;
  text-align: center;

  h1{
    font-size: 9vw;
    padding: 0 0 4vh 0;
  }

  & > div{
    display: block;
