  overflow-x: hidden;
}

.process-step{
  text-align: center;
  position: relative;

  p{
    position: absolute;
    top: -6vh;
    left: 5vw;
    z-index: 3;
    font-size: 3.2em;
    font-family: druk, sans-serif;
    font-weight: bold;
  }
}


//PRICING

