  position: relative;
  text-align: center;

  p{
    position: absolute;
    right: 8vw;
    top: 4vh;
    font-size: 1.5em;
    z-index: 3;
  }
}


//SIGNUP

.signup{
  padding-top: 18vh;
  .c-marquee{
    border: none;
  }
}


//FOOTER

.footer{
  text-align: center;
  padding: 20vh 0 0 0;

  & > div:first-child{
    padding-bottom: 10vh;
  }

  .copy-info{
